import React from "react";
import { withRouter } from "next/router";
import AnimationLinks from "../components/AnimationLinks";
import MainLayout from "../components/MainLayout";
import { animationLinks } from "../js/globals";

export default withRouter(
  class Animation extends React.Component {
    static async getInitialProps(ctx) {
      return { data: await ctx.query };
    }
    render() {
      let Animation;
      if (this.props.data.name) {
        const nameLength = this.props.data.name.split("").length;
        const linkName =
          this.props.data.name.split("")[0].toUpperCase() +
          this.props.data.name
            .split("")
            .splice(1, nameLength)
            .join("");

        Animation = require("../components/animation/" + linkName).default;
      } else {
        Animation = () => {
          return <p>Animation Page</p>;
        };
      }

      return (
        <div>
          <AnimationLinks />
          <Animation />
        </div>
      );
    }
  }
);
